<template>
  <div class="zd-common-component">
    <div class="zd-common-clearFix">
      <Button type="primary" class="zd-common-fl zd-common-pl11 zd-common-pr11 zd-common-mr10" @click="exportHandle">
        <Icon type="ios-open-outline" class="zd-common-fs18 zd-common-mr5 zd-common-fl"/>导出
      </Button>
      <div class="zd-common-fr">
        <Select v-model="warehouse" :clearable="true" placeholder="全部仓库" class="zd-common-input-h38 zd-common-w200 zd-common-fl zd-common-mr10">
          <Option v-for="item in warehouseArr" :value="item.id" :key="item.id">{{ item.name }}</Option>
        </Select>
        <!--<Select v-model="type" placeholder="选择物品分类" class="zd-common-input-h38 zd-common-w200 zd-common-fl zd-common-mr10" clearable>
          <Option v-for="item in classifyArr" :value="item[0]" :key="item[0]">{{ item[1] }}</Option>
        </Select>-->
        <div class="zd-cm-tree zd-common-w300 zd-common-fl zd-common-mr10">
          <treeselect style="height: 38px;" v-model="type" :multiple="true" :options="treeOptions" :normalizer="normalizer" :limit="2" :limitText="limitHandle" noOptionsText="暂无数据" :clearable="false" placeholder="选择物品分类" @close="onSelect"/>
        </div>
        <Select v-model="status" placeholder="全部状态" :clearable="true" class="zd-common-input-h38 zd-common-w200 zd-common-fl zd-common-mr10">
          <Option value="0">库存不足</Option>
          <Option value="1">库存超出</Option>
        </Select>
        <Input v-model="name" :maxlength="50" placeholder="物品名称" class="zd-common-input-h38 zd-common-w200 zd-common-fl zd-common-mr10"/>
        <Button type="primary" class="zd-common-fl" @click="selectHandle" :loading="selectLoading">
          <span v-if="!selectLoading">查询</span>
          <span v-else>正在查询...</span>
        </Button>
      </div>
    </div>
    <div class="zd-common-mt16">
      <Table ref="currentRowTable" border :columns="columns" :data="data"></Table>
      <!--<div class="zd-common-tc zd-common-mt20">
        <Page :total="total" :current="pageNum" :page-size="pageSize" @on-change="changePage" @on-page-size-change="changePageSize" show-sizer show-elevator show-total></Page>
      </div>-->
    </div>
  </div>
</template>
<script>
  import Treeselect from '@riophae/vue-treeselect'
  import '@riophae/vue-treeselect/dist/vue-treeselect.css'
  export default {
    components: {
      Treeselect
    },
    data() {
      return {

        //列表
        columns: [
          {
            type: 'index',
            align: 'center',
            title: '序号',
            width:65
          },
          {
            title: '物品名称',
            align: 'center',
            key: 'name',
          },
          {
            title: '物品编码',
            align: 'center',
            key: 'code'
          },
          {
            title: '物品类别',
            align: 'center',
            key: 'classify'
          },
          {
            title: '品牌',
            align: 'center',
            key: 'brand',
          },
          {
            title: '规格/型号',
            align: 'center',
            key: 'model',
          },
          {
            title: '单位',
            align: 'center',
            key: 'unit',

          },
          {
            title: '最高库存',
            align: 'center',
            key: 'maxInventory',
          },
          {
            title: '最低库存',
            align: 'center',
            key: 'minInventory',
          },
          {
            title: '现存量',
            align: 'center',
            key: 'standingStock',
            render: (h, params) => {
              if(params.row.standingStock>params.row.maxInventory){
                return h('div', [
                  h('span', {
                    class:'zd-common-error-c',
                  },params.row.standingStock),
                ]);
              }else if(params.row.standingStock<params.row.minInventory){
                return h('div', [
                  h('span', {
                    class:'zd-common-success-c',
                  },params.row.standingStock),
                ]);
              }else{
                return h('div', [
                  h('span', {
                  },params.row.standingStock),
                ]);
              }
            }
          },
          {
            title: '状态',
            align: 'center',
            key: 'status',
            render: (h, params) => {
              if(params.row.standingStock>params.row.maxInventory){
                return h('div', [
                  h('span', {
                    class:'zd-common-error-c',
                  },params.row.status),
                ]);
              }else if(params.row.standingStock<params.row.minInventory) {
                return h('div', [
                  h('span', {
                    class:'zd-common-success-c',
                  },params.row.status),
                ]);
              }else{
                return h('div', [
                  h('span', {
                  },params.row.status),
                ]);
              }
            }
          }
        ],
        data: [],
        pageSize:10,
        pageNum:1,
        total:0,
        selectLoading:false,

        //查询
        dateOptions: this.dateRange,
        warehouseArr:[],
        warehouse:'',
        type:null,
        typeIdArr:[],
        status:'',
        name:'',
        treeOptions:[],
        normalizer(node) {
          return {
            id: node.id,
            label: node.name,
            children: node.children,
          }
        }
      }
    },
    created(){
      this.getChargedStoreRoom().then((response)=>{
        this.warehouseArr = response.data;
      });
      this.warningStatistics(this.warehouse,this.typeIdArr.join(','),this.status,this.name);
      this.getKind();
    },
    methods: {
      //物品种类
      onSelect(value,instanceId){
        this.typeIdArr = value;
      },
      //获取分类
      getKind(){
        this.treeOptions= [];
        this.getGoodsKindTree().then((res)=>{
          this.treeOptions = res.data;
          // this.recursionFnOne(this.treeOptions,res.data);
        })
      },
      //列表
      warningStatistics(warehouseIds,goodsKindIds,status,goodsName) {
        this.data = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!warningStatistics.action',
          data:{
            warehouseIds:warehouseIds,
            goodsKindIds:goodsKindIds,
            status:status,
            goodsName:goodsName,
          },
        })
          .then((response) => {
            let aData = response.data;
            if(aData && aData.length>0){
              aData.map((item)=>{
                this.data.push({
                  id:item.id,
                  name:item.name,
                  code:item.code,
                  classify:item.goodsKindName,
                  brand:item.brand,
                  model:item.size,
                  unit:item.unit,
                  maxInventory:item.max,
                  minInventory:item.min,
                  standingStock:item.inventory,
                  status:item.statusName,
                })
              })
            }
            this.total = aData.totalRecord;
            this.selectLoading = false;
          })
          .catch((error) => {
            this.selectLoading = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //分页
      changePage(num) {
        this.pageNum = num;
        this.warningStatistics(this.warehouse,this.typeIdArr.join(','),this.status,this.name);
      },
      changePageSize(num) {
        this.pageSize = num;
        this.warningStatistics(this.warehouse,this.typeIdArr.join(','),this.status,this.name);
      },
      //查询
      selectHandle(){
        this.selectLoading = true;
        this.warningStatistics(this.warehouse,this.typeIdArr.join(','),this.status,this.name);
      },
      //导出
      exportHandle(){
        location.href = this.serverPath + "/sr/storeroomData!exportWarningStatistics.action?warehouseIds="+this.warehouse+"&goodsKindIds="+this.typeIdArr.join(',')+"&status="+this.status+"&goodsName="+this.name;
      }
    },
    mounted() {

    }
  }
</script>
<style lang="less" scoped>


</style>



